import React, {ChangeEvent, useCallback} from 'react'
import {Checkbox, IconButton} from '@material-ui/core'
import {Delete} from '@material-ui/icons'
import {EditableSpan} from "../../components/common/EditableSpan";
import {TaskStatuses, TaskType} from "../../../bll/Api";

type TaskPropsType = {
    task: TaskType
    todolistId: string
    changeTaskStatus: (id: string, status: TaskStatuses, todolistId: string) => void
    changeTaskTitle: (taskId: string, newTitle: string, todolistId: string) => void
    // removeTask: (taskId: string, todolistId: string,task: TaskType) => void
    removeTask: (taskId: string, todolistId: string) => void
}
// export const Task = React.memo((props: TaskPropsType) => {
export const Task = React.memo(({task,todolistId,changeTaskStatus,changeTaskTitle,removeTask}:TaskPropsType) => {
    const onClickHandler = useCallback(() => {
    return removeTask(task._id, todolistId)}, [task._id, todolistId]
    // return removeTask(task._id, todolistId,task)}, [task._id, todolistId]
    );

    const onChangeHandler = useCallback((e: ChangeEvent<HTMLInputElement>) => {
        let newIsDoneValue = e.currentTarget.checked
        changeTaskStatus(task._id, newIsDoneValue ? TaskStatuses.Completed : TaskStatuses.New, todolistId)
    }, [task, todolistId]);

    const onTitleChangeHandler = useCallback((newValue: string) => {
        changeTaskTitle(task._id, newValue, todolistId)
    }, [task, todolistId]);

    return <div key={task._id} className={task.status === TaskStatuses.Completed ? 'is-done' : ''}>
        <Checkbox
            checked={task.status === TaskStatuses.Completed}
            color="primary"
            onChange={onChangeHandler}
        />

        <EditableSpan value={task.title} onChange={onTitleChangeHandler}/>
        <IconButton onClick={onClickHandler}>
            <Delete/>
        </IconButton>
    </div>
})
