import React, {useCallback, useEffect} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Grid, Paper} from '@material-ui/core'
import {Redirect} from "react-router-dom";
import {AppRootStateType} from "../../../bll/store";
import {
    addTodolistTC, changeTodolistFilterAC, changeTodolistTitleTC,
    fetchTodolistsTC,
    FilterValuesType,
    removeTodolistTC,
    TodolistDomainType
} from "../../../bll/todolists-reducer";
import {addTaskTC, removeTaskTC, TasksStateType, updateTaskTC} from "../../../bll/tasksReduser";
import {TaskStatuses, TaskType} from "../../../bll/Api";
import {AddItemForm} from "../../components/common/AddItemForm";
import {Todolist} from "./todo";
import {log} from "util";

type PropsType = {
    addTodolist: (title: string) => void
    removeTodolist: (todolistId: string) => void
    todolistFromGroup: Array<string>
}

export const TodolistsList: React.FC<PropsType> = (props: PropsType) => {
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(fetchTodolistsTC())
    }, [dispatch ])
    const todolists = useSelector<AppRootStateType, Array<TodolistDomainType>>(state => state.todoList)
    const isLoggedIn = useSelector<AppRootStateType, boolean>(state => state.auth.isLoggedIn);
    const tasks = useSelector<AppRootStateType, TasksStateType>(state => state.tasks)
    const groupTodo = useSelector<AppRootStateType, Array<TodolistDomainType>>(state => state.groups.todoListForGroup)

    // const groupTodo:Array<any>  = props.todolistFromGroup && props.todolistFromGroup.map(tlG => {
    //     return  todolists.find((todo) => tlG === todo.id)
    // })


    //const removeTask = useCallback(function (id: string, todolistId: string, task: TaskType) {
    const removeTask = useCallback(function (id: string, todolistId: string) {
        // dispatch(removeTaskTC(id, todolistId, task))
        dispatch(removeTaskTC(id, todolistId))
    }, [dispatch])
    const addTask = (title: string, todolistId: string) => {
        dispatch(addTaskTC(title, todolistId))
    }
    const changeStatus = useCallback(function (id: string, status: TaskStatuses, todolistId: string) {
        dispatch(updateTaskTC(id, {status}, todolistId))
    }, [])
    const changeTaskTitle = useCallback(function (id: string, newTitle: string, todolistId: string) {
        dispatch(updateTaskTC(id, {title: newTitle}, todolistId))
    }, [])
    const changeFilter = useCallback(function (value: FilterValuesType, todolistId: string) {
        dispatch(changeTodolistFilterAC(todolistId, value))
    }, [])
    const changeTodolistTitle = useCallback(function (id: string, title: string) {
        dispatch(changeTodolistTitleTC(id, title))
    }, [])
    const removeTodolist = useCallback(function (id: string) {
        //todolistId
        // dispatch(removeTodolistTC(id))
        props.removeTodolist(id)
    }, [])
    const addTodolist = useCallback((title: string) => {
        // dispatch(addTodolistTC(title))
        props.addTodolist(title)
    }, [dispatch])

    console.log('groupTodo', groupTodo);
    return <>
        <Grid container style={{padding: '20px'}}>
            <AddItemForm addItem={addTodolist}/>
        </Grid>
        {<Grid container spacing={3}>
            {
                groupTodo &&  groupTodo.map((tl) => {
                    let allTodolistTasks = tasks[tl.id]
                    console.log(tl.id)
                    return <Grid item key={tl.id}>
                        <Paper style={{padding: '10px'}}>
                            <Todolist
                                todolist={tl}
                                tasks={allTodolistTasks}
                                removeTask={removeTask}
                                changeFilter={changeFilter}
                                addTask={addTask}
                                changeTaskStatus={changeStatus}
                                removeTodolist={removeTodolist}
                                changeTaskTitle={changeTaskTitle}
                                changeTodolistTitle={changeTodolistTitle}
                            />
                        </Paper>
                    </Grid>
                })
            }
        </Grid>}
    </>
}
